import React, { useState } from "react";
import styled, { css } from "styled-components";
import ReactDOM from 'react-dom';
import ControlledCarousel from "../ControlledCarousel";

const Outside = styled.div`
    font-size: ${({theme}) => theme.verticalMode ? '2.4vw' : '2vh'} !important;
    cursor: auto;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: ${props=>props.isSelected ? 'flex' : 'none' };
    justify-content: center;
    align-items: center;
    background-color: #00000092;
    border-radius: 0 !important;
`;

const Main = styled.div`
    background-color: ${props=>props.theme.darkTheme.bg4};
    display: flex;
    align-items: center;
    color: ${props=>props.theme.darkTheme.color};
    position: relative;

    ${props => props.theme.verticalMode ? css`
        width: 80vw;
        flex-direction: column;
    ` : css`
        width: 60vw;
    `};
    height: 80vh;

    & > div{
        ${props => props.theme.verticalMode ? css`
            width: 90%;
            margin-bottom: 5%;
        ` : css`
            width: 55%;
        `};
    }
    
    & > div:first-child{
        display: flex;
        justify-content: center;
        flex-shrink: 0;
    }
`;

const Close = styled.img`
    position: fixed;
    cursor: pointer;
    border-radius: 0 !important;

    ${props => props.theme.verticalMode ? css`
        width: 3vh;
        height: 3vh;
        top: 6vh;
        right: 10vw;
    ` : css`
        width: 3vw;
        height: 3vw;
        top: 10vh;
        right: 15vw;
    `};
`;

const Title = styled.div`
    
`;

function Project(props){
    return ReactDOM.createPortal(
        <Outside isSelected={props.isSelected}>
            <Main>
                <div>
                    <ControlledCarousel data={props.data}/>
                </div>
                <div>
                    <Title>
                        {props.data.title}
                    </Title>
                </div>
            </Main>
            <Close src={`${process.env.PUBLIC_URL}/images/close.png`} onClick={props.selectOff} />
        </Outside>, document.body
    );
}

export default Project;